.Tilt {
  background: #c6ffdd; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f7797d,
    #fbd786,
    #c6ffdd
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

nav {
  display: flex;
}
.logInfo {
  flex: 2 1 300px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.form {
  width: 700px;
}

